import { HashLocationStrategy, LocationStrategy } from '@angular/common';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {IncomeCalculatorComponent} from "./income-calculator/income-calculator.component";
import {LoanCalculatorComponent} from './loan-calculator/loan-calculator.component';
import { MatrixCalculatorComponent } from './matrix-calculator/matrix-calculator.component';


const routes: Routes = [
  {path:'income',component:IncomeCalculatorComponent },
  {path:'loan',component:LoanCalculatorComponent },
  {path:'matrix',component:MatrixCalculatorComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers:[
    {
      provide:LocationStrategy, useClass: HashLocationStrategy
    }
  ]
})
export class AppRoutingModule { }
