import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-income-calculator',
  templateUrl: './income-calculator.component.html',
  styleUrls: ['./income-calculator.component.css']
})
export class IncomeCalculatorComponent implements OnInit {

  constructor() { }

  superRate = 0.095;

  calculatorType = 0;

  afterTaxValue = 0;

  afterTaxSuper = false;

  afterTaxFrequency = 1;

  beforeTaxValue = 0;

  beforeTaxSuper = false;

  beforeTaxFrequency = 1;

  incomePerYear = 0;

  selections = [
    {value:'1', viewValue:"Income After Tax"},
    {value:'2',viewValue: "Income Before Tax"}
  ]

  frequencies = [
    {value:'1',viewValue:"Yearly"},
    {value:'12',viewValue: "Monthly"},
    {value:'26',viewValue:"Fortnightly"},
    {value:'52',viewValue: "Weekly"},
    {value:'260',viewValue:"Daily"},
    {value:'1976',viewValue: "Hourly"}
  ]

  ngOnInit(): void {
  }

  onCalTypeChange(e){
    this.calculatorType = e.value;
    this.incomePerYear = 0;
  }

  calculateAfterTax(){
    let income = this.afterTaxValue*this.afterTaxFrequency;

    if (this.afterTaxSuper){
      income = (1-this.superRate)*income;
    }

    if(income <= 18200){
      income = income - (0.02*income)
    }else if (income <= 45000) {
      income = income - 0.19*(income - 18200)- (0.02*income)
    }else if (income <= 120000) {
      income = income - 5092 - (0.325*(income - 45000))- (0.02*income)
    }else if (income <= 180000) {
      income = income - 29467 - (0.37*(income - 120000))- (0.02*income)
    }else {
      income = income - 51667 - (0.45*(income - 180000))- (0.02*income)
    }

    this.incomePerYear = income;
  }

  calculateBeforeTax(){
    let income = this.beforeTaxValue*this.beforeTaxFrequency;

    if (this.beforeTaxSuper){
      income = (1-this.superRate)*income;
    }

    if(income <= 17836){
      income = income / 0.98;
    }else if (income <= 39008) {
      income = (income - (0.19*18200))/0.79;
    }else if (income <= 88133) {
      income = (income + 5092 - (0.325*45000))/0.655;
    }else if (income <= 124733) {
      income = (income + 29467 - (0.37*120000))/0.61
    }else {
      income = (income + 51667 - (0.45*180000))/0.53;
    }

    this.incomePerYear = income;
  }
}
